import { AppState, AppReducer } from 'store/constants';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';
import { MapMaskType } from 'components/AppLayout/AppLayout';

const routeToMapVisibilityMap: Record<string, boolean> = {
    [ROUTES.CART]: true,
    [ROUTES.NO_AREA]: true,
};

const routeToMapMaskMap: Record<string, MapMaskType> = {
    [ROUTES.CART]: 'right',
    [ROUTES.NO_AREA]: 'full',
};

const isMapVisible = (state: AppState) => routeToMapVisibilityMap[selectPathname(state)] || false;

const getMapMaskType = (state: AppState): MapMaskType => routeToMapMaskMap[selectPathname(state)] || 'none';

const isOverlayVisible = (state: AppState) => {
    switch (selectPathname(state)) {
        case ROUTES.CART:
            return state.cartPage.isOrderStatusWaiting;
        default:
            return false;
    }
};

const getOverlayText = (state: AppState) => {
    switch (selectPathname(state)) {
        case ROUTES.CART: {
            return state.cartPage.isOrderStatusWaiting ? 'Оформляем заказ' : '';
        }
        default:
            return '';
    }
};

export const layoutView: AppReducer = (state) => {
    const { layout } = state;

    layout.overlay.isVisible = isOverlayVisible(state);
    layout.overlay.text = getOverlayText(state);
    layout.background.map.isVisible = isMapVisible(state);
    layout.background.map.maskType = getMapMaskType(state);
};
