import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks';
import { selectOverlay, selectBackgroundMap } from 'store/selectors';
import { Header } from 'components/Header/Header';
import { Spinner } from 'components/Spinner/Spinner';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';
import { transition } from 'style/const/transition';
import gradientBackground from 'assets/images/background-tv-sber.svg';
import mapImage from 'assets/images/map.png';

const Container = styled.div`
    position: relative;

    width: 100%;
    height: 100%;

    overflow: hidden;

    background-color: ${color.background.default};
    background-image: url('${gradientBackground}');
    background-position: center;
    background-size: cover;
`;

const Layer = css`
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
`;

export type MapMaskType = 'none' | 'right' | 'full';

const mapMaskMap: Record<MapMaskType, string> = {
    none: 'linear-gradient(transparent 0%, transparent 100%)',
    right: 'linear-gradient(to right, transparent 50%, #fff 100%)',
    full: 'linear-gradient(to bottom, rgba(0, 0, 0, 0.2) 0%, rgba(0, 0, 0, 1) 50%, rgba(0, 0, 0, 0.2) 100%)',
};

interface MapBackgroundProps {
    isVisible: boolean;
    maskType: MapMaskType;
}

const MapBackground = styled.div<MapBackgroundProps>`
    ${Layer}
    z-index: 1;

    background-image: url('${mapImage}');
    opacity: ${({ isVisible }) => Number(isVisible)};
    mix-blend-mode: luminosity;

    transition: opacity ${transition.default};

    ${({ maskType }) =>
        css`
            mask-image: ${mapMaskMap[maskType]};
        `};
`;

const AppContent = styled.div`
    ${Layer}
    z-index: 2;

    top: ${layout.headerHeightPx}px;

    height: ${layout.contentHeightPx}px;
`;

const StyledHeader = styled(Header)`
    position: absolute;
    z-index: 3;
    left: 0;
    right: 0;
`;

interface OverlayProps {
    isVisible: boolean;
}

const Overlay = styled.div<OverlayProps>`
    ${Layer}
    z-index: 4;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;

    background-color: ${color.background.default};
    background-image: url('${gradientBackground}');
    background-position: center;
    background-size: cover;

    transition: opacity ${transition.default};

    ${({ isVisible }) =>
        !isVisible &&
        css`
            pointer-events: none;
            opacity: 0;
        `}
`;

const OverlayText = styled(BodyText1)`
    margin-top: 32px;

    color: ${color.text.primary};
`;

export const AppLayout: React.FC = ({ children }) => {
    const { isVisible: isMapVisible, maskType } = useSelector(selectBackgroundMap);
    const { isVisible: isOverlayVisible, text: overlayText } = useSelector(selectOverlay);

    return (
        <Container>
            <Overlay isVisible={isOverlayVisible}>
                <Spinner />
                {overlayText && <OverlayText>{overlayText}</OverlayText>}
            </Overlay>
            <StyledHeader />
            <AppContent>{children}</AppContent>
            <MapBackground isVisible={isMapVisible} maskType={maskType} />
        </Container>
    );
};
